.timeline {
  position: relative;
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding: 15px 0;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background: #5393a8;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
}

.container {
  padding: 3vh 1.5vw;
  position: relative;
  background: inherit;
  width: 50%;
}

.container.left {
  left: 0;
}

.container.right {
  left: 50%;
}

.container::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  top: calc(50% - 8px);
  right: -8px;
  background: #ffffff;
  border: 2px solid #5393a8;
  border-radius: 16px;
  z-index: 1;
}

.container.right::after {
  left: -8px;
}

.container::before {
  content: '';
  position: absolute;
  width: 50px;
  height: 2px;
  top: calc(50% - 1px);
  right: 8px;
  background: white;
  z-index: 1;
}

.container.right::before {
  left: 8px;
}

.container .date {
  position: absolute;
  display: inline-block;
  top: calc(50% - 1.5vh);
  text-align: center;
  font-size: 0.7em;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 1;
}

.container.left .date {
  right: -10vw;
}

.container.right .date {
  left: -10vw;
}

.container .icon {
  position: absolute;
  display: inline-block;
  width: 2vw;
  height: 4vh;
  padding: 9 0;
  top: calc(50% - 2vh);
  background: white;
  border: 2px solid white;
  border-radius: 40px;
  text-align: center;
  font-size: 0.7em;
  color: white;
  z-index: 1;
}

.container.left .icon {
  right: 30px;
}

.container.right .icon {
  left: 30px;
}

.container .content {
  padding: 2vh 2vh 2vh 2vh;
  border-radius: 5px;
  background: #8e45a6;
  background: -webkit-linear-gradient(to right,#4b9fa9,#8e45a6);
  background: linear-gradient(to right,#4b9fa9,#8e45a6);
  box-shadow: 0 10px 10px -2px rgba(0,0,0,.29);
  cursor:pointer;
  border: 0 !important;
  position: relative;
  border-radius: 0 500px 500px 0;
}

.container.right .content {
  padding: 4vh 4vh 4vh 12vh;
  border-radius: 500px 0 0 500px;
}

.container.left .content {
  padding: 4vh 4vh 4vh 2vh;
}


.container .content h2 {
  margin: 0 0 10px 0;
  font-size: 1.7em;
  font-weight: normal;
  color: white;
}

.container .content p {
  margin: 0;
  font-size: 0.7em;
  line-height: 22px;
  color: white;
}
